"use client"

import Image from "next/image"
import { useTheme } from "next-themes"
import { Button } from "@/components/ui/button"
import { ThemeToggle } from "@/components/theme-toggle"
import { Mail } from "lucide-react"
import { useEffect, useState } from "react"

export function Header() {
  const { theme, resolvedTheme } = useTheme()
  const [mounted, setMounted] = useState(false)

  useEffect(() => {
    setMounted(true)
  }, [])

  // Determine which logo to show based on resolved theme
  const logoSrc = mounted && resolvedTheme === "dark"
    ? "/logo-white.png"
    : "/logo-black.png"

  return (
    <header className="sticky top-0 z-50 w-full border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
      <div className="container mx-auto flex h-14 items-center justify-between px-4 sm:px-6 lg:px-8">
        {/* Logo */}
        <div className="flex items-center">
          {mounted ? (
            <Image
              src={logoSrc}
              alt="Logo"
              width={280}
              height={84}
              className="h-16 w-auto sm:h-20 md:h-24"
              priority
            />
          ) : (
            <div className="h-16 w-56 sm:h-20 sm:w-72 md:h-24 md:w-80 animate-pulse bg-muted rounded" />
          )}
        </div>

        {/* Right side: Theme toggle and Contact button */}
        <div className="flex items-center gap-2 sm:gap-3">
          <ThemeToggle />
          <Button
            size="default"
            variant="outline"
            className="gap-2 text-sm sm:text-base whitespace-nowrap hover:bg-accent transition-colors"
          >
            <Mail className="h-4 w-4 shrink-0" />
            <span className="hidden sm:inline">Contact</span>
          </Button>
        </div>
      </div>
    </header>
  )
}

